


.journal__main-content{
    display: flex;
}

.journal__sidebar{
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-right: 10px;
    padding-left: 10px;
    width: 450px;
}

.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;

    h3{
        font-weight: lighter;

    }
}

.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    width: 100%;
    flex-direction: column;

    transition: color .3s ease;

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}


.journal__entries{
    margin-top: 30px;
    flex: 1 1 auto;
    overflow: hidden;
}

.journal__entry{
    background-color: white;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    color: $dark-grey;
    overflow: hidden;
    margin-bottom: 10px;
    cursor: pointer;
}

.journal__entry-picture{
    height: 75px;
    width: 75px;
}

.journal__entry-body{
    padding: 10px;
}

.journal__entry-title{
    font-size: 14px;
    font-weight: bold;
}

.journal__entry-content{
    font-size: 10px;
}


.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;

    span{
        font-size: 12px;
    }
}

